import React from 'react'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div>
    <h1>Welcome</h1>
    <p>This is a brand new site where I play around with decentralized web protocols and other fun stuff.</p>
    <p>I built it using Gatsby and React.</p>
    <p>Insert circa-mid-90's Under Construction animated gif here.</p>
    <p>There are several ways you can view this site. Using the traditional Internet you can visit its Github Pages home <a href="https://mrjmd.github.io/cyuio/">here</a>.</p>
    <p>This site is also available for viewing via IPFS by going <a href="https://gateway.ipfs.io/ipfs/QmXZ24XB5t9FD7gCxjefYhibU3kFomY8ExgnJbzZyqshrA/">here</a>.</p>
    <p>If you are using Beaker Browser you can also access a p2p version of this site on the DAT Protocol <a href="dat://196c3058aaa82d964b6b4084f0076b3e4a5c45809cebc80f8a814cbce30af14a/">here</a>.</p>
  </div>
)

export default IndexPage



